import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import "../../stylesheets/CourseSelection.css";
import logo from "../../logo.svg";
import CardDeck from "react-bootstrap/CardDeck";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Card";
import { useState, useEffect } from 'react';
import { useHistory, useParams } from "react-router-dom";
import axios from "axios";
import Header from '../courseRating/header';
import SideBar from "../courseRating/sideBar";
import UserDetails from "../profileManagement/UserDetails"

function ViewGrade() {
  const [show, setShow] = useState(false);
  const [courseName, setCourseName] = useState('');
  const [userList, setUserList] = useState([]);
  const [firstName, setFirstName] = useState('')
  const history = useHistory();
  const param = useParams();
  axios.get("http://localhost:3000/"+param.id).then((response) => {
          setCourseName(response.data.course.name)
          console.log(courseName)
      })


      axios.get("http://localhost:3000/userInformation/getUserDetails/" + UserDetails.getId()).then((response) => {
        console.log(response.data.result.FirstName)
        setFirstName(response.data.result.FirstName)
    })



  useEffect(() => {
      axios.get("http://localhost:3000/grade/all/"+param.id).then((response) => {
          setUserList(response.data.grade)
          console.log(response.data.grade)
          
      })
  },
  [])

  const redirectToMarkingPage = (id) => history.push('/grades/markingPage/' + id);
     return (
        <>
        <body>
        <Header/>   
        <SideBar/>
        
          <section className="gradeSide">
          
            <section className="gradeList">
              <table className="gradingTable">
              <tbody>
                <tr className="tableHeader">
                  <th>Course</th>
                  <th>Grade</th>
                  <th>Feedback</th>
                </tr>
              {userList.map((val, key) => {
                
                if(val.studentName==firstName){
                  
                  return(
                
                    <tr>
                      <td>{val.courseName}</td>
                      <td>{val.gradeMark}</td>
                      <td>{val.feedback}</td>
                    </tr>
                     )

                }
                 
               
                })}
             </tbody> 

              
               
             </table>
            </section>
          </section>
        </body>
      </>
    );
}

export default ViewGrade;