import "../../stylesheets/application.css";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { useState } from 'react';
import { useHistory, useParams } from "react-router-dom";
import SideBar from "../courseRating/sideBar";
import Header from "../courseRating/header";
import Axios from 'axios';
import UserDetails from "../profileManagement/UserDetails";

function Application() {
    const [studentEmail, setStudentEmail] = useState('');
    const [courseId, setCourseId] = useState('');
    const [userEmail, setUserEmail] = useState('');
    const [paramEmail, setParamEmail]=useState('');
    const history = useHistory();
    const param = useParams();

    const redirectToCourseHome = () => history.push('/application');
    const redirectToAppliedCourses = () => history.push('/appliedCourses');
    Axios.get("http://localhost:3000/courseApplication/appliedCourses").then((response) => {
        setCourseId(response.data.course.courseId)
        setUserEmail(response.data.course.studentEmail)
        console.log(courseId)

})

Axios.get("http://localhost:3000/userInformation/getUserDetails/" + UserDetails.getId()).then((response) => {
        console.log(response.data.result.FirstName)
        setParamEmail(response.data.result.Email)
    })



    const handleSubmit = () => {
        if(!studentEmail)
        {
            alert("Please provide email address to apply")

        }
        else
        {
            if(courseId==param.id&& userEmail==paramEmail)
            {
                alert("Already applied!!")
            }
            else
            {
                Axios.post('http://localhost:3000/courseApplication/apply/' + param.id, {
                    studentEmail: studentEmail
                })

            }
           
        
        alert("Thank you for the Applying.");
        redirectToCourseHome();

        }
        
    }

    const handleCancel = () => {
        alert("Canceled. You can continue next time.");
        redirectToCourseHome();
    }

    return (
        <body>
            <Header />
            <SideBar />
            <div className="content-application">
                <div className="survey-application">
                    <h1>Course Application</h1>
                    <Form>
                        
                        <Form.Group controlId="formBasicInput">
                            <Form.Label>Please provide your email address</Form.Label>
                            <Form.Control as="input"  placeholder="Give your emailId" onChange={(event) => { setStudentEmail(event.target.value) }} />
                        </Form.Group>
                        <Button className="cancelButton" onClick={handleCancel}>
                            Cancel
                        </Button>
                        <Button className="applyButton" onClick={handleSubmit}>
                            apply
                        </Button>
                    </Form>
                </div>
            </div>
        </body>
    )
}

export default Application;